import "./App.css";
import {
    formatTime,
    getHoliday,
    getNextDeparture,
    isWeekendTraffic,
    relativeTime,
} from "./services/timetable";
import useForceUpdate from "./utils/hooks/useForceUpdate";
import useInterval from "./utils/hooks/useInterval";
import Collapsable from "./components/Collapsable";
import FullTimeTable from "./components/FullTimeTable";
import dayjs from "dayjs";
import React from "react";
import { useInput } from "./utils/hooks/useInput";
import { useLocalStorage } from "./utils/hooks/useLocalStorage";
import { useDepartureTimes } from "./services/useDepartureTimes";

type Settings = {
    lumabrygganMinutes: number;
    barnängenMinutes: number;
    henriksdalMinutes: number;
};

function App() {
    const now = dayjs();
    const departureTimes = useDepartureTimes(now);
    const weekendTraffic = isWeekendTraffic(now);
    const holiday = getHoliday(now);
    const forceUpdate = useForceUpdate();
    const [settings, setSettings] = useLocalStorage<Settings>("settings", {
        lumabrygganMinutes: 0,
        barnängenMinutes: 0,
        henriksdalMinutes: 0,
    });
    const lumabrygganInput = useInput(String(settings.lumabrygganMinutes));
    const barnängenInput = useInput(String(settings.barnängenMinutes));
    const henriksdalInput = useInput(String(settings.henriksdalMinutes));

    useInterval(() => {
        forceUpdate();
    }, 1000);

    const handleSettingsSubmit: React.FormEventHandler<HTMLFormElement> = (
        e
    ) => {
        e.preventDefault();

        setSettings({
            lumabrygganMinutes: Number(lumabrygganInput.value ?? 0),
            barnängenMinutes: Number(barnängenInput.value ?? 0),
            henriksdalMinutes: Number(henriksdalInput.value ?? 0),
        });
    };

    const departureHeader = (
        stopName: string,
        nextDeparture1: dayjs.Dayjs,
        nextDeparture2: dayjs.Dayjs,
        walkTimeMinutes: number
    ) => {
        const minsUntilDeparture1 = nextDeparture1.diff(now, "minute");
        const minsUntilDeparture2 = nextDeparture2.diff(now, "minute");

        const tooLateDeparture1 = minsUntilDeparture1 < walkTimeMinutes;
        const tooLateDeparture2 = minsUntilDeparture2 < walkTimeMinutes;
        console.log(tooLateDeparture1, tooLateDeparture2);

        return (
            <p className="App-depRow">
                <strong>{stopName}</strong>

                <span className="App-depRow-departure">
                    <span className="App-depRow-time">
                        {formatTime(nextDeparture1)}
                    </span>

                    <span
                        className={tooLateDeparture1 ? "App-running-late" : ""}
                    >
                        {tooLateDeparture1 ? (
                            <img src="running-late.svg" height={14} />
                        ) : null}
                        {relativeTime(nextDeparture1, now)}
                    </span>
                </span>
                <span className="App-depRow-departure">
                    <span className="App-depRow-time">
                        {formatTime(nextDeparture2)}
                    </span>

                    <span
                        className={tooLateDeparture2 ? "App-running-late" : ""}
                    >
                        {tooLateDeparture2 ? (
                            <img src="running-late.svg" height={14} />
                        ) : null}
                        {relativeTime(nextDeparture2, now)}
                    </span>
                </span>
            </p>
        );
    };

    return (
        <div className="App">
            <header className="App-header">
                <img
                    src={process.env.PUBLIC_URL + "/android-chrome-512x512.png"}
                    alt="Sjöstadsbåten"
                    width="64"
                    height="64"
                    className="App-logo"
                />
                <span className="App-logoText">Sjöstadsbåten</span>
            </header>

            <main className="App-main">
                <h2 className="App-heading">Nästa avgång</h2>

                <div className="App-departures">
                    <Collapsable
                        name="Lumabryggan"
                        header={departureHeader(
                            "Lumabryggan",
                            departureTimes.lumabryggan1,
                            departureTimes.lumabryggan2,
                            settings.lumabrygganMinutes
                        )}
                    >
                        <FullTimeTable
                            stop="lumabryggan"
                            selectedTime={formatTime(
                                departureTimes.lumabryggan1
                            )}
                        />
                    </Collapsable>

                    <Collapsable
                        name="Barnängen"
                        header={departureHeader(
                            "Barnängen",
                            departureTimes.barnängen1,
                            departureTimes.barnängen2,
                            settings.barnängenMinutes
                        )}
                    >
                        <FullTimeTable
                            stop="barnängen"
                            selectedTime={formatTime(departureTimes.barnängen1)}
                        />
                    </Collapsable>

                    <Collapsable
                        name="Henriksdal"
                        header={departureHeader(
                            "Henriksdal",
                            departureTimes.henriksdal1,
                            departureTimes.henriksdal2,
                            settings.henriksdalMinutes
                        )}
                    >
                        <FullTimeTable
                            stop="henriksdal"
                            selectedTime={formatTime(
                                departureTimes.henriksdal1
                            )}
                        />
                    </Collapsable>
                </div>

                <Collapsable
                    name="Inställningar"
                    header={
                        <h6 className="App-settings-heading">Inställningar</h6>
                    }
                >
                    <form
                        className="App-settings-panel"
                        onSubmit={handleSettingsSubmit}
                    >
                        <p className="">
                            <small>
                                Hur lång tid tar det för dig att gå till
                                hållplatsen?
                            </small>
                        </p>
                        <div className="form-row">
                            <label>Lumabryggan</label>
                            <input
                                type="number"
                                className="App-settings-mins"
                                placeholder="Minuter"
                                {...lumabrygganInput.bind}
                            />
                        </div>
                        <div className="form-row">
                            <label>Barnängen</label>
                            <input
                                type="number"
                                className="App-settings-mins"
                                placeholder="Minuter"
                                {...barnängenInput.bind}
                            />
                        </div>
                        <div className="form-row">
                            <label>Henriksdal</label>
                            <input
                                type="number"
                                className="App-settings-mins"
                                placeholder="Minuter"
                                {...henriksdalInput.bind}
                            />
                        </div>
                        <div className="center">
                            <button type="submit">Spara</button>
                        </div>
                    </form>
                </Collapsable>

                <p className="App-timeTableType">
                    <small>
                        {weekendTraffic
                            ? holiday
                                ? `Visar helgtidtabell (${holiday.holiday})`
                                : "Visar helgtidtabell"
                            : "Visar vardagstidtabell"}
                        <br />
                        Gäller t.o.m. 24 juni 2022
                        <br />
                        <small>
                            <a
                                href="https://github.com/aolde/sjostadsbaten"
                                target="_blank"
                                rel="noreferrer"
                                className="App-github"
                            >
                                Github
                            </a>
                        </small>
                    </small>
                </p>
            </main>
        </div>
    );
}

export default App;
